













interface SimilarColorCardThis {
    distance: number;
    colorName: string;
    roundedDistance: () => number
}

export default {
    props: {
        colorName: {
            type: String,
            required: true,
        },
        distance: {
            type: Number,
            required: true,
        },
    },
    computed: {
        roundedDistance() {
            return Math.round((this as SimilarColorCardThis).distance);
        }
    }
};
